.bg1{
	position: relative;
	top: 3rem;
	height:15rem;
	background-image:linear-gradient(90deg,rgba(0,0,0,0.75) 0%,rgba(0,0,0,0) 100%),url(../images/bg-p-c.jpg) ;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;		
	color: aliceblue;
	text-align: center;
}
.bg1 h1{
	position: relative;
	top: 6rem;
}


.products1{
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(260px,1fr));
	justify-items: center;
	row-gap: 50px;
	margin-top: 50px;
	
}
.products1 img{
	padding: 50px;
}
.product1{
	max-width: 320px;
	align-self: center;
	
}
.bg2{
	height:15rem;
	background-image:linear-gradient(90deg,rgba(0,0,0,0.75) 0%,rgba(0,0,0,0) 100%),url(../images/bg-p-c.jpg) ;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;	
	margin-top: 1.5rem;	
	color: aliceblue;
	text-align: center;
}
.bg2 h1{
	position: relative;
	top: 6rem;
}
.products2{
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(260px,1fr));
	justify-items: center;
	row-gap: 50px;
	margin-top: 50px;	
}
.product2{
	max-width: 320px;
	align-self: center;	
}
.product2 p{
	margin-bottom: 50px;
}
.products2 img{
	padding: 50px;
}