* {
   margin: 0;
   padding: 0;
   text-decoration: none;
   box-sizing: border-box;
   scroll-behavior: smooth;
   width: 100%;
}
body::-webkit-scrollbar {
   display: none;
}
a {
   cursor: pointer;
}
h1 {
   font-size: 1.9rem;
   font-weight: 600;
   margin-bottom: 0.5rem;
}
h2 {
   font-size: 1.1rem;
   font-weight: 400;
   margin-bottom: 1rem;
}
.button {
   width: fit-content;
   padding: 10px 20px;
   border-radius: 5px;
   border: solid 1px grey;
   margin-top: 15px;
}
.button:hover {
   background: rgb(251, 234, 234);
   box-shadow: 1px 1px 1px 1px rgb(99, 98, 98);
}
