.bg{
	position: relative;
	top: 3rem;
	height:15rem;
	background-image:linear-gradient(90deg,rgba(0,0,0,0.75) 0%,rgba(0,0,0,0) 100%),url(../images/bg-p-c.jpg) ;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;		
	color: aliceblue;
	text-align: center;
}
.bg h1{
	position: relative;
	top: 6rem;
}
.certificates{
	position: relative;
	top: 7rem;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(260px,1fr));
	justify-items: center;
}
