.content_2 {
   position: absolute;
   top: 106vh;
}
.content_2 span {
   display: block;
   background-color: #f2f2f2 ;
   font-size: 1.3rem;
   text-align: center;
   font-weight: 700;
   height: fit-content;
   padding: 0.75rem;
   width: 100vw;
}
.content_3{  
   width: 100vw;
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(450px,1fr) );   
   column-gap: 20px;
   background-color: #fff;  
}
.content_3 .imaa{
   max-width: 100vw;
   align-self: center;
   padding-left: 20px;
}
.content_3 .txt{
 padding: 50px;
 max-width: 100vw;
 align-self: center;
 
}
.content_3 .txt h1{
   font-size: 1.5rem;
}
.part3 .products{
   width: 100vw;
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(350px,1fr) );   
   background-color: #f2f2f2;   
}
.part3 .products .product-image{
   max-width: 400px;
   align-self: center;
   justify-self:center;
   padding: 20px 60px;    
}
.part3 .products .product-image img{
   border-radius: 15px;
}
.part4{
   text-align: center;
   line-height: 6.5rem;
}
.testimonials{
   width: 100vw;
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(350px,1fr) );   
   list-style: none;
   column-gap: 50px;
   row-gap: 20px;
   margin-bottom: 50px;
}
.testimonials li p{
 width: 350px;
 line-height: 1rem; 
 padding: 0 10px;
}
.testimonials li{  
   justify-self:center;
   width: fit-content;
   border: 1px solid black;
   background-color: #f2f2f2;
   border-radius: 10px;
}
.testimonials li img{
   height: 2rem;
   width: 2rem;
}
#name{
   font-weight: 900;
}

.contact-details div i img{
height: 1em;
width: 1em;
border-radius: 5px;
}
.contact-details div p{
   color: #494848;
}

.part5{
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(350px,1fr) );
   column-gap: 40px;
   background-color: #bcbaba;
   padding: 10px 10px 70px 10px;
}
form{
   margin: auto;
}

form input, textarea{
   margin-top: 10px;
   border-radius: 6px;
}
form input{
   height: 2.5rem;
   
}
form input, textarea, .bt{
   margin-left: 25px;
   
   max-width: 85%;   
}
.contact-details{
   margin-left: 25px;
   margin-top: 25px;
   margin-bottom: 25px;
   max-width: 80%;
}
.d{
   margin-bottom: 15px;
}