.header {
   position: fixed;
   width: 100vw;
   font-weight: 500;
   background-color: #fff;
   box-shadow: 0 1px 0 rgb(0 0 0 / 10%);
   height: 3rem;
   z-index: 999;
}
.logo img{
   max-width: 3rem;
   border-radius: 20%;
}
.container {
   display: flex;
   justify-content: space-between;
   width: 80vw;
   align-items: center;
   margin: auto;
   height: 3rem;
}
.menu_list {
   text-align: right;
}

.menu_list a,
.mobile_menu a {
   color: #000;
   margin-right: 1.5rem;
   font-weight: 900;
}
.menu_list a:hover,
.mobile_menu a:hover{
   text-decoration: underline;
}
.hamburger {
   display: none;
   color: #000;
   font-size: 2rem;
   padding:0 45px;
   position: absolute;
   left:6.2rem;
   top:0.2em;
}
@media screen and (max-width: 1024px) {
   .menu_list {
      display: none;
   }
   .hamburger {
      display: block;
    /* position: relative; */    
   }

   .mobile_menu {
      display: block;
      position: absolute;
      display: flex;
      flex-direction: column;
      left:0px;
      top: 3rem;
      max-width: 100%;
      height: fit-content;
      background: rgb(253, 253, 253);
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px; 
      box-shadow: 0 1px 0 rgb(0 0 0 / 10%);
   }
   .mobile_menu a {
      color: black;
      margin: 10px 50px;
   }
   .mobile_menu a:hover {
      text-decoration: underline;
   }
}
