

.about{
	position: absolute;
	top:3rem;
	height:25rem;
	background-image:linear-gradient(90deg,rgba(0,0,0,0.75) 0%,rgba(0,0,0,0) 100%), url(../images/wp.jpg);
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;	
	display: flex;
	background-color: rgba(255,255,255,0);
	
}
.content{
	margin: auto;
	margin-left: 20%;
}
#about{
	color: aliceblue;
	font-weight: 800;
	font-size: 3.5rem;
}
.profile{
   position: absolute;
   top: 25rem;
   width: 100vw;
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(350px,1fr) );   
}
.imgg{
   max-width: 300px;
   align-self: center;
   padding: 50px 0px 0px 50px;
}
.profile_content{
 padding: 50px;
 max-width: 100vw;
 align-self: center;
 
}
.profile_content p{
	color: rgb(62, 61, 61);
}
.our_mission{
	position: absolute;
	top:70rem;
	background-color: #f2f2f2; 
}
.our_mission h1{
	text-align: center;
	line-height: 8.0rem;
	margin-bottom: 50px;
}
.our_mission p{
	padding: 0 50px 90px;
	max-width: 1050px;
	margin: auto;
	font-size: 1.2rem;
	color: rgb(80, 80, 80);
}

@media screen and (min-width:710px) {
	.our_mission{
		position: absolute;
		top:50rem;
	}
}